import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import $ from "jquery";
import DynamicTable from './DynamicTable';
const TABLE_COLUMNS = [
    '',
    '',
    ' ',
];

function editRow(e) {
      // ,   
      let t = e.target || e.srcElement;
      //  
      let elm_name = t.tagName.toLowerCase();
      //   -   
      if(elm_name === 'input') {return false;}
      let val = $(this).html();
      let code = '<input type="text" id="edit" value="'+val+'" />';
      $(this).empty().append(code);
      $('#edit').focus();
      $('#edit').blur(function()  {
        let val = $(this).val();
        $(this).parent().empty().html(val);
      });
  }


$(window).keydown(function(event){
  //   
  if(event.keyCode === 13) { //  Enter
    $('#edit').blur();  //    
  }
});


class Send extends Component {
	constructor(props){
		super(props);
		this.state={
		  displayedTable:[]
		};
		this.onMyClickAddRow = this.onMyClickAddRow.bind(this);
	}
	componentDidMount() {
	  fetch('/send')
	  .then(res => res.json())
	  .then(displayedTable => {        
	    this.setState({ displayedTable });
	    console.log(this.state.displayedTable);
	  });
	  
	}
	componentDidUpdate() {
		let t =  document.getElementById("dyntab");
	  let tds = t.getElementsByTagName("td");
	    for (var i=0; i<tds.length; i++){

	        tds[i].addEventListener('click',editRow);
	    }
	}
	onMyClickAddRow() {

	    document.getElementById("dyntab").insertRow(-1).innerHTML = '<td>QQ</td><td>qq</td><td>Qq</td>';

	    let t =  document.getElementById("dyntab");
	    let tds = t.getElementsByTagName("td");
	    for (var i=0; i<tds.length; i++){

	        tds[i].addEventListener('click',editRow);
	    }
	}
	render() {
		return (
			<div className="pos-center-block">
				<div className="custom-file">
					<input type="file" className="custom-file-input" id="customFile" accept=".xls, .xls"/>
					<label className="custom-file-label" htmlFor="customFile"> </label>
				</div>
				<DynamicTable data={this.state.displayedTable} columns={TABLE_COLUMNS}/>
				<div className="btnAdd">
					<input type="button" className="btn btn-info" onClick={this.onMyClickAddRow} value="" name="submit"/>
				</div>
				<div className="form-group">
				    <label htmlFor="qqqq"> </label>
				    <textarea className="form-control" id="qqqq" rows="5"></textarea>
				    <Link to="/accept"><input type="button" className="btn btn-success" value="" name="submit"/></Link>
				</div>
			</div>

		);
	}
}

export default Send;



