import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import DynamicTable from './DynamicTable';
const TABLE_COLUMNS = [
    '',
    '',
    ' ',
    ' '
];
const data = [
    ['Alexandr', 'Volik','0930685396','SMS'],
    ['Alexandr', 'Volik','0930685396','SMS'],
    ['Alexandr', 'Volik','0930685396','SMS'],
    ['Alexandr', 'Volik','0930685396','SMS']
  ];

class Accept extends React.Component {
            
  constructor(props){
    super(props);
    this.state={
      displayedTable: []
    };
  }

  componentDidMount() {
    /*fetch('/accept')
      .then(res => res.json())
      .then(accept => this.setState({ accept }));
*/
      fetch('/accept')
      .then(res => res.json())
      .then(displayedTable => {        
        this.setState({ displayedTable });
        console.log(this.state.displayedTable);
      });
  }

  send() {
    alert("hi");
  };
  cancel() {
    alert("cancel");
  };
  render() {

    return (
      <div className="pos-center-block">
        <div className="table-responsive">
          <DynamicTable data={this.state.displayedTable} columns={TABLE_COLUMNS}/>
        </div>
          <span>  : </span><br/>
          <span> viber : </span><br/>
          <span>     :  </span><br/>
          <span>    : </span><br/>
          <Link to="/send"><button onClick={this.cancel} className="btn btn-danger my-btn-pos-1"></button></Link>
          <Link to="/send"><button onClick={this.send} className="btn btn-success my-btn-pos-2"></button></Link>     
      </div>
    );
  }
}

export default Accept;



